.full-height {
  height: 100vh;
}

.content {
  padding: 0.5em;
  display: flex;
  flex-direction: column;
  height: 92%;
}

.content > .row {
  padding: 0.2em;
}

.quit-button {
  flex: 0 1 5%;
}

.question-card {
  flex: 20 1 auto;
}

.answer-buttons {
  flex: 0 1 20%;
}

.special {
  display: flex;
}

